<template>
  <div id="intrument-trend">
    <img src="../../../assets/images/title_yqzjqs.png">
    <Echart id="InstrumenTrendChart" ref="InstrumenTrendRef" :options="options" height="280px" width="610px" />

  </div>
</template>

<script>
import { AddEquTrendApi } from "@/api/instrument.js"

import Echart from '@/common/echart'

export default {
  data () {
    return {
      options: {},
      addData: [],
      addXData: [],
      handleData: [],
      timing: null
    }
  },
  components: {
    Echart,
  },
  mounted () {

    AddEquTrendApi().then(res => {
      const { code, data } = res
      if (code == 0) {
        this.addData = data.map(item => item.value)
        this.addXData = data.map(item => item.name)
        this.initChart()
      }
    })
  },
  watch: {

  },
  beforeDestroy () {
    this.timing = null
    clearInterval(this.timing)
  },
  methods: {
    initChart () {
      this.options = {
        legend: {
          x: 'right',
          top: '5%',
          // left: '15%',
          textStyle: {
            color: '#ECF8FF',
            fontSize: 14,
            padding: [0, 8, 0, 8]
          }
        },
        grid: {
          top: '20%',
          left: '10%',
          right: '4%',
          bottom: '30%',
        },
          tooltip: {
          trigger: 'axis',
          formatter: function (params) {
            return '<div>' + params[0].name + '<br>' + params[0].marker + params[0].seriesName + ':' + params[0].value + '</div>' 
            // +
            //   '<div>' + params[1].marker + params[1].seriesName + ':' + params[1].value + '</div>'
          }
        },
        xAxis: {
          data: this.addXData,
          axisLine: {
            lineStyle: {
              color: '#A9B1B6',
            }
          },
          axisTick: {//刻度
            show: false,
          },
        },
        yAxis: {
          // max: 1000,
          splitLine: {
            lineStyle: {
              type: 'dashed',
              color: '#496070'
            }
          },
          axisLine: {
            lineStyle: {
              color: '#aaa',
            }
          }
        },
        series: [
          {
            name: "新增仪器",
            type: 'line',
            data: this.addData,
            color: '#28C4D8',
            areaStyle: {
              normal: {
                color: new this.$echarts.graphic.LinearGradient(0, 0, 0, 1, [{
                  offset: 0,
                  color: 'rgba(40,196,216 ,0.3)'
                },
                {
                  offset: 1,
                  color: 'rgba(40,196,216 ,0)'
                }
                ], false),
                shadowColor: 'rgba(40,196,216,0.9)',
                shadowBlur: 20
              }
            },
          },
          {
            name: "处置仪器",
            type: 'line',
            data: this.handleData,
            color: '#55AEFF',
            areaStyle: {
              normal: {
                color: new this.$echarts.graphic.LinearGradient(0, 0, 0, 1, [{
                  offset: 0,
                  color: 'rgba(85,174,255 ,0.3)'
                },
                {
                  offset: 1,
                  color: 'rgba(85,174,255 ,0)'
                }
                ], false),
                shadowColor: 'rgba(85,174,255 ,0.9)',
                shadowBlur: 20
              }
            },
          },
          {
            type: 'effectScatter',
            showEffectOn: 'render',
            rippleEffect: {
              period: 10,
              scale: 1,
              brushType: 'fill'
            },
            hoverAnimation: true,
            itemStyle: {
              normal: {
                color: '#FFF',
                // shadowBlur: 10,
                // shadowColor: '#333'
              }
            },
            data: this.addData,
          },
          {
            type: 'effectScatter',
            showEffectOn: 'render',
            rippleEffect: {
              period: 10,
              scale: 1,
              brushType: 'fill'
            },
            hoverAnimation: true,
            itemStyle: {
              normal: {
                color: '#FFFFFF',
                // shadowBlur: 10,
                // shadowColor: '#333'
              }
            },
            data: this.handleData,
          },

        ]
      }
      this.timeFun()
    },

    timeFun () {
      const myChart = this.$echarts.init(document.getElementById('InstrumenTrendChart'))
      var app = {
        currentIndex: -1,
      }
      this.timing = setInterval(() => {
        var dataLen = this.options.series[0].data.length
        // 取消之前高亮的图形
        myChart.dispatchAction({
          type: 'downplay',
          seriesIndex: 0,
          dataIndex: app.currentIndex
        })
        app.currentIndex = (app.currentIndex + 1) % dataLen
        // 高亮当前图形
        myChart.dispatchAction({
          type: 'highlight',
          seriesIndex: 0,
          dataIndex: app.currentIndex,
        })
        // 显示 tooltip
        myChart.dispatchAction({
          type: 'showTip',
          seriesIndex: 0,
          dataIndex: app.currentIndex
        })
      }, 2000)
    }

  }
}
</script>

<style lang="scss" scoped>
#intrument-trend {
  width: 618px;
  height: 289px;
  background: rgba($color: #072036, $alpha: 0.46);
  padding-top: 17px;
  border-top: 3px solid;
  border-image: linear-gradient(to left, #052348, rgba(171, 218, 255, 0.58)) 1;
}
</style>
