    <div class="video-container" ref="container">
      <div :class="index==activeIndex ? 'video-item active' : 'video-item'" v-for="(item, index) in videoInfo" :key="index">
        <div v-if="index==playIndex">
          <jessibuca-demo :equNumber="item.videoSrc" styleValue="width:300px;height:200px;" />
        </div>
        <div v-else>
          <img :src="item.imgSrc" alt="" style="width:300px;height:200px;" @click="activeClick(index)">
        </div>
        <div class="video-description">
          <div class="top-text">
            <div :title="item.videoTitle">
              {{ item.videoTitle }}
            </div>
          </div>
        </div>
      </div>

    </div>

  </div>
</template>

<script>
import JessibucaDemo from "@/components/JessibucaDemo.vue";

export default {
  data() {
    return {
      // 实时流直播地址
      videoInfo: [
        // {
        //   videoSrc: "43018112501320000009/43018112501320000009",
        //   videoTitle: "测试用",
        // },
        {
          videoSrc: "34020000001180000012/34020000001320000007",
          videoTitle: "力学实验室",
          imgSrc:
            "http://113.247.236.85:18080/api/device/query/snap/34020000001180000012/34020000001320000007",
        },
        {
          videoSrc: "34020000001180000012/34020000001320000003",
          videoTitle: "化学实验室",
          imgSrc:
            "http://113.247.236.85:18080/api/device/query/snap/34020000001180000012/34020000001320000003",
        },
        {
          videoSrc: "34020000001180000012/34020000001320000012",
          videoTitle: "水泥实验室",
          imgSrc:
            "http://113.247.236.85:18080/api/device/query/snap/34020000001180000012/34020000001320000012",
        },
        {
          videoSrc: "34020000001180000012/34020000001320000013",
          videoTitle: "土工实验室",
          imgSrc:
            "http://113.247.236.85:18080/api/device/query/snap/34020000001180000012/34020000001320000013",
        },
        {
          videoSrc: "34020000001180000012/34020000001320000014",
          videoTitle: "管材实验室",
          imgSrc:
            "http://113.247.236.85:18080/api/device/query/snap/34020000001180000012/34020000001320000014",
        },
        {
