          videoTitle: "燃烧实验室",
          imgSrc:
            "http://113.247.236.85:18080/api/device/query/snap/34020000001180000012/34020000001320000023",
        },
      ],
      timing: null,
      startVal: 0,
      duration: 2000,
      activeIndex: 0,
      playIndex: -1,
    };
  },
  components: {
    JessibucaDemo,
  },
  mounted() {
    this.timeFun();
    this.$once("hook:beforeDestroy", () => {
      clearInterval(this.timing);
      this.timing = null;
    });
  },

  methods: {
    timeFun() {
      this.timing = setInterval(() => {
        if (this.activeIndex == this.videoInfo.length - 1) {
          //当前高亮为最后一个
          this.activeIndex = 0;
        } else {
          this.activeIndex = this.activeIndex + 1;
        }
        if (this.activeIndex == 0) {
          this.$refs.container.scrollTop = 0;
        } else {
          this.$refs.container.scrollTop = this.$refs.container.scrollTop + 260;
        }
      }, 10000);
    },
    activeClick(index) {
      this.activeIndex = index;
      this.playIndex = index;
    },
    imgBtn(){
      window.open('http://113.247.236.85:18080/');
    }
  },
};
</script>

