<template>
  <div id="instrument-count">

    <div class="data-overview">
      <div class="total instrument-total" @click="handleClick(1)">
        <div class="total-text">
          仪器设备总量<span class="total-num">
            <CountTo :startVal='startVal' :endVal='equData.equAmount' :duration='duration' separator="," />
          </span>台
        </div>
      </div>
      <div class="total original-total">
        <div class="total-text">
          仪器原值总值<span class="total-num">
            <CountTo :startVal='startVal' :endVal='equData.equOriginValue' :duration='duration' separator="," />
          </span>
        </div>
      </div>
    </div>

    <div :class="'data-item' + ' ' + item.className" v-for="(item, index) in instrumentData" :key="index"
      :style="item.style" @click="handleClick(item.name)">
      <img :src="item.imgSrc" class="data-item-img">
      <div class="data-item-text">
        <div class="data-item-name">
          <div>{{ item.name }}</div>
        </div>
        <div class="data-item-account">
          <div>
            <div>
              <CountTo :startVal='startVal' :endVal='item.num' :duration='duration' separator="," />
            </div>台
          </div>
        </div>
      </div>
    </div>
    <el-drawer title="详情" :visible.sync="drawerVisible" size="1000px" append-to-body modal-append-to-body @close="() => {
      this.query = Object.assign({}, this.initQuery)
    }">
      <dv-loading v-if="Loading">数据加载中...</dv-loading>
      <div v-else>
        <el-table ref="table" :data="tableValue" tooltip-effect="dark" style="width: 100%;">
          <el-table-column label="序号" type="index" width="70"></el-table-column>
          <el-table-column label="仪器编号" prop="equipmentNumber" />
          <el-table-column label="仪器名称" prop="equipmentName" />
          <el-table-column label="仪器类型" prop="equipmentType" />
          <el-table-column label="归属部门" prop="deptName" />
          <el-table-column label="存放地点" prop="storagePlace" />
          <el-table-column label="检定日期" prop="checkTime" />
          <el-table-column label="检定有效期" prop="checkValidityTime" />
        </el-table>
        <div class="table-pagination">
          <div class="data-num">共<span> {{ query.total }} </span>条数据</div>
          <el-pagination :current-page="query.pageIndex" :page-size="query.pageSize" :total="query.total" background
            layout="sizes, prev, pager, next, jumper" @size-change="handleSizeChange"
            @current-change="paginationCurrentChange" />
        </div>
      </div>
    </el-drawer>
  </div>
</template>

<script>
import { equipmentEntityPage } from "@/api/home.js"
import { EquDataOverviewApi, EquTypeDataApi, queryEquDetailApi } from "@/api/instrument.js"
import CountTo from "vue-count-to"
import cl from "@/assets/images/icon_cailiao.png"
import ql from "@/assets/images/icon_qiaoliang.png"
import gl from "@/assets/images/icon_gonglu.png"
import sp from "@/assets/images/icon_shipin.png"
import jc from "@/assets/images/icon_jiance.png"
import sl from "@/assets/images/icon_shuili.png"
import dzxx from "@/assets/images/icon_dianzixinxi.png"
import sd from "@/assets/images/icon_suidao.png"
import tz from "@/assets/images/icon_tezhong.png"
import aq from "@/assets/images/icon_anquan.png"
import jg from "@/assets/images/icon_jiegou.png"
import dj from "@/assets/images/icon_diji.png"
export default {
  data () {
    this.initQuery = {
      pageIndex: 1,
      pageSize: 10,
      total: 0,
    }
    return {
      Loading: false,
      instrumentData: [
        { name: "材料类检测仪器", num: 0, className: "blue", style: { left: '20px', top: '160px' }, imgSrc: cl },
        { name: "桥梁检测仪器", num: 0, className: "green", style: { left: '260px', top: '70px' }, imgSrc: ql },
        { name: "公路检测仪器", num: 0, className: "blue", style: { left: '810px', top: '70px' }, imgSrc: gl },
        { name: "食品检测仪器", num: 0, className: "green", style: { left: '1020px', top: '160px' }, imgSrc: sp },
        { name: "监测类仪器", num: 0, className: "blue", style: { left: '1020px', top: '360px' }, imgSrc: jc },
        { name: "水利检测仪器", num: 0, className: "green", style: { left: '750px', top: '500px' }, imgSrc: sl },
        { name: "电子信息类检测仪器", num: 0, className: "blue", style: { left: '260px', top: '500px' }, imgSrc: dzxx },
        { name: "隧道检测仪器", num: 0, className: "green", style: { left: '20px', top: '360px' }, imgSrc: sd },
        { name: "特种设备与钢结构检测", num: 0, className: "blue", style: { left: '250px', top: '270px' }, imgSrc: tz },
        { name: "其他仪器", num: 0, className: "green", style: { left: '520px', top: '160px' }, imgSrc: aq },
        { name: "结构检测仪器", num: 0, className: "green", style: { left: '520px', top: '360px' }, imgSrc: jg },
        { name: "地基检测仪器", num: 0, className: "blue", style: { left: '780px', top: '270px' }, imgSrc: dj },
      ],
      startVal: 0,
      duration: 2000,
      equData: {},
      query: Object.assign({}, this.initQuery),
      drawerVisible: false,
      tableValue: [],
      index: 0,
    }
  },
  components: {
    CountTo
  },
  mounted () {
    this.initData()
  },
  methods: {
    toQuery () {
      if (this.index == 1) {
        equipmentEntityPage(this.query).then(res => {
          if (res.code == 0) {
            this.Loading = false
            this.tableValue = res.data.records
            this.query.total = res.data.total
          }
        }).finally(() => {
          this.Loading = false
        })
      } else {
        queryEquDetailApi({ ...this.query, equType: this.index }).then(res => {
          if (res.code == 0) {
            this.Loading = false
            this.tableValue = res.data.records
            this.query.total = res.data.total
          }
        }).finally(() => {
          this.Loading = false
        })
      }
    },
    paginationCurrentChange (pageIndex) {
      this.query = { ...this.query, pageIndex: pageIndex }
      this.toQuery()
    },
    handleSizeChange (pageSize) {
      this.query = { ...this.query, pageSize: pageSize }
      this.toQuery()
    },
    handleClick (index) {
      this.index = index
      this.drawerVisible = true
      this.toQuery()
    },
    initData () {
      EquDataOverviewApi().then(res => {
        const { code, data } = res
        if (code == 0) {
          this.equData = data
        }
      })

      EquTypeDataApi().then(res => {
        const { code, data } = res
        if (code == 0) {
          this.instrumentData.forEach(item => {
            data.forEach(ele => {
              if (ele.name == item.name || ele.name.indexOf(item.name) > -1) {
                item.num = ele.value
              }
            })
          })
        }
      })

    }
  }
}
</script>

<style lang="scss" scoped>
#instrument-count {
  width: 1251px;
  height: 630px;
  background: url("~@/assets/images/img_bg_1.png") no-repeat bottom;
  position: relative;

  .data-overview {
    position: absolute;
    top: 5px;
    right: 0px;
    display: flex;

    .total {
      width: 300px;
      height: 45px;
      background: url("~@/assets/images/total_bg.png") no-repeat bottom;
      text-align: center;

      .total-text {
        color: #dde6ed;
        font-size: 16px;

        .total-num {
          color: #96fff9;
          font-size: 34px;
          font-weight: bold;
          margin: 0 5px;
        }
      }
    }

    .instrument-total {
      margin-right: 14px;
    }
  }

  .data-item {
    position: absolute;
    // width: 123px;
    height: 116px;

    .data-item-img {
      position: absolute;
      top: 10px;
      left: 38px;

      animation-name: beat;
      animation-duration: 2s;
      /*设置秒数*/
      animation-timing-function: linear;
      /*速度曲线*/
      animation-iteration-count: infinite;
      /*播放次数*/
      animation-direction: alternate;
      /*逆向播放*/
      animation-play-state: running;
    }

    @keyframes beat {
      // 0% {
      //   transform: scale(1, -1);
      // }

      // 100% {
      //   transform: translate(0px, 10px);
      //   // transform: scale(1.2);
      // }
      100% {
        transform: scale(-1, 1);
        transform: translate(0px, 15px);
      }
    }

    .data-item-text {
      // position: absolute;
      margin-left: 120px;

      .data-item-name {
        // width: 180px;
        height: 13px;
        background: linear-gradient(90deg,
            rgba(8, 97, 183, 0.75) 0%,
            rgba(14, 95, 191, 0.2) 100%);
        font-size: 14px;
        padding: 0 10px 0 10px;
        transform: skew(-45deg);

        div {
          transform: skew(45deg);
        }
      }

      .data-item-account {
        margin-top: 14px;
        height: 13px;
        background: linear-gradient(90deg,
            rgba(8, 97, 183, 0.75) 0%,
            rgba(14, 95, 191, 0.2) 100%);
        padding: 0 20px;
        transform: skew(-45deg);

        &>div {
          transform: skew(45deg);
          display: flex;

          div {
            font-size: 20px;
            font-weight: 600;
          }
        }
      }
    }
  }

  .data-item.green {
    background: url("~@/assets/images/img_green.png") no-repeat;
  }

  .data-item.blue {
    background: url("~@/assets/images/img_bule.png") no-repeat;
  }
}
</style>
