<template>
  <div id="detection" ref="appRef">
    <div class="bg">
      <video src="../../assets/bg_2.mp4" class="bjimg" autoplay loop muted></video>
      <dv-loading v-if="loading">Loading...</dv-loading>
      <div v-else class="host-body">
        <Header headerType="instrument" />
        <div class="echart-body">
          <div class="body-left">
            <instrument-count class="body-left-top" />
            <div style="display:flex;">
              <instrument-trend style="margin-right: 16px;" />
              <instrument-aptitude />
            </div>
          </div>

          <div class="body-right">
            <instrument-video class="body-right-top" />
            <origan-instrument />
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import drawMixin from "@/utils/drawMixin";
import Header from "@/components/Header";
import InstrumentCount from "./components/InstrumentCount.vue";
import InstrumentTrend from "./components/IntrumentTrend.vue";
import InstrumentAptitude from "./components/InstrumentAptitude.vue";
import InstrumentVideo from "./components/InstrumentVideo_flv.vue";
// import InstrumentVideo from "./components/InstrumentVideo_mp4.vue";
import OriganInstrument from "./components/OriganInstrument.vue";
export default {
  mixins: [drawMixin],
  data() {
    return {
      loading: true,
    };
  },
  components: {
    Header,
    InstrumentCount,
    InstrumentTrend,
    InstrumentAptitude,
    InstrumentVideo,
    OriganInstrument,
  },
  mounted() {
    this.cancelLoading();
  },
  beforeDestroy() {
    clearInterval(this.timing);
  },
  methods: {
    cancelLoading() {
      setTimeout(() => {
        this.loading = false;
      }, 500);
    },
  },
};
</script>

<style lang="scss" scoped>
// @import '@/assets/scss/index.scss';
#detection {
  color: #d3d6dd;
  width: 1920px;
  height: 1080px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transform-origin: left top;
  overflow: hidden;

  .bg {
    width: 100%;
    height: 100%;
    // background-image: url("~@/assets/images/bg_3.png");
    // background-size: cover;
    // background-position: center center;
  }
  .bjimg {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -10;
    object-fit: cover;
  }
}

.echart-body {
  margin-top: 14px;
  padding: 0 32px;
  display: flex;
  justify-content: space-between;

  .body-left {
    .body-left-top {
      margin-bottom: 17px;
    }
  }

  .body-right {
    .body-right-top {
      margin-bottom: 20px;
    }
  }
}
</style>
