<template>
  <div id="origan-intrument">
    <img src="../../../assets/images/title_fzjgyq.png">

    <dv-scroll-board :config="config" style="width: 540px;height:330px;margin: 0 auto;" :key="randomKey" />
  </div>
</template>

<script>
import { EquBranchApi } from "@/api/instrument.js"
export default {
  data () {
    return {
      options: {},
      config: {
        header: ['分类机构名称', '仪器数量', '原值'],
        data: [],
        headerBGC: "rgba(9,47,111,0.6)",
        oddRowBGC: "#0A1A37",
        evenRowBGC: "#0D2145",
        columnWidth: [80, 200],
        index: true,
        waitTime: 1000
      },
      randomKey: Math.random(),
    }
  },
  components: {},
  mounted () {
    EquBranchApi().then(res => {
      const { code, data } = res
      if (code == 0) {
        this.config.data = data.map(item => [
          item.name, item.value, item.number
        ])
        this.randomKey = Math.random() //刷新组件
      }
    })


  },
  methods: {
    // initChart () {

    // }
  }
}
</script>

<style lang="scss" scoped>
#origan-intrument {
  width: 566px;
  height: 416px;
  background: rgba($color: #072036, $alpha: 0.46);
  padding-top: 17px;
  border-top: 3px solid;
  border-image: linear-gradient(to left, #052348, rgba(171, 218, 255, 0.58)) 1;
}
</style>
