<template>
  <div id="intrument-aptitude">
    <img src="../../../assets/images/title_sbyzfb.png">

    <Echart id="InstrumentAptitude" ref="InstrumentAptitudeRef" :options="options" height="280px" width="620px" />

  </div>
</template>

<script>
import Echart from "@/common/echart";
import { EquOriginApi } from "@/api/instrument";

export default {
  data() {
    return {
      options: {},
      yData: [],
      xData: [],
    };
  },
  components: {
    Echart,
  },
  mounted() {
    EquOriginApi().then((res) => {
      const { code, data } = res;
      if (code == 0) {
        console.log(data);
        let tempArr = data;
        tempArr.splice(5, 1);
        // console.log(tempArr)
        // console.log(tempArr.map(item => item.name))
        let temp = tempArr[9];
        tempArr[9] = tempArr[8];
        tempArr[8] = temp;

        this.xData = tempArr.map((item) => item.name);
        this.yData = tempArr.map((item) => item.value);
        this.initChart();
      }
    });
  },
  beforeDestroy() {
    this.timing = null;
    clearInterval(this.timing);
  },
  methods: {
    initChart() {
      this.options = {
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "shadow",
          },
        },
        legend: {
          x: "right",
          top: "0%",
          // left: '15%',
          textStyle: {
            color: "#ECF8FF",
            fontSize: 14,
            padding: [0, 8, 0, 8],
          },
        },
        grid: {
          top: "15%",
          right: "5%",
          left: "9%",
          bottom: "40%",
        },
        xAxis: [
          {
            type: "category",
            data: this.xData, //dataX,
            axisLine: {
              lineStyle: {
                color: "#496070",
              },
            },
            axisLabel: {
              interval: 0,
              margin: 10,
              color: "#A9B1B6",
              textStyle: {
                fontSize: 11,
              },
              rotate: "-45",
            },
            axisTick: {
              //刻度
              show: false,
            },
          },
        ],
        yAxis: [
          {
            name: "数量",
            nameTextStyle: {
              color: "#A9B1B6",
              fontSize: 12,
            },
            axisLabel: {
              padding: [3, 0, 0, 0],
              formatter: "{value}",
              color: "#A9B1B6",
              textStyle: {
                fontSize: 11,
              },
            },
            axisTick: {
              show: true,
            },
            axisLine: {
              lineStyle: {
                color: "rgba(66, 192, 255, .3)",
              },
            },
            splitLine: {
              lineStyle: {
                type: "dashed",
                color: "#496070",
              },
            },
          },
        ],

        series: [
          {
            name: "设备",
            type: "bar",
            data: this.yData, //dataY,
            barWidth: "10",
            itemStyle: {
              normal: {
                color: new this.$echarts.graphic.LinearGradient(
                  0,
                  0,
                  0,
                  1,
                  [
                    {
                      offset: 0,
                      color: "rgba(93,194,255, 1)", // 0% 处的颜色
                    },
                    {
                      offset: 0.98,
                      color: "rgba(93,194,255, 0)", // 100% 处的颜色
                    },
                  ],
                  false
                ),
                shadowColor: "rgba(93,194,255, 1)",
                shadowBlur: 4,
              },
            },
            label: {
              normal: {
                show: true,
                lineHeight: 10,
                formatter: "{c}",
                position: "top",
                textStyle: {
                  color: "#fff",
                  fontSize: 10,
                },
              },
            },
            zlevel: 99,
          },
          {
            // name: '背景',
            type: "bar",
            barWidth: "50%",
            xAxisIndex: 0,
            yAxisIndex: 0,
            barGap: "-200%",
            itemStyle: {
              normal: {
                color: "rgba(12,48,82,0.55)",
              },
            },
            zlevel: 0,
          },
        ],
      };
      this.timeFun();
    },
    timeFun() {
      const myChart = this.$echarts.init(
        document.getElementById("InstrumentAptitude")
      );
      var app = {
        currentIndex: -1,
      };
      this.timing = setInterval(() => {
        var dataLen = this.options.series[0].data.length;
        // 取消之前高亮的图形
        myChart.dispatchAction({
          type: "downplay",
          seriesIndex: 0,
          dataIndex: app.currentIndex,
        });
        app.currentIndex = (app.currentIndex + 1) % dataLen;
        // 高亮当前图形
        myChart.dispatchAction({
          type: "highlight",
          seriesIndex: 0,
          dataIndex: app.currentIndex,
        });
        // 显示 tooltip
        myChart.dispatchAction({
          type: "showTip",
          seriesIndex: 0,
          dataIndex: app.currentIndex,
        });
      }, 2000);
    },
  },
};
</script>

<style lang="scss" scoped>
#intrument-aptitude {
  width: 640px;
  height: 289px;
  background: rgba($color: #072036, $alpha: 0.46);
  padding-top: 17px;
  border-top: 3px solid;
  border-image: linear-gradient(to left, #052348, rgba(171, 218, 255, 0.58)) 1;
}
</style>
